import express from "express";
const router = express.Router();


import { createExposure, getExposures } from "../covid/logic/main";

router.post("/write", async function(req, res, next) {
  const r = await createExposure(req.body.param);
  res.json(r);
  next();
});

router.post("/datapoint", async function(req, res, next) {
  const r = await getExposures();
  res.json(r);
  next();
});



